@import url(https://fonts.googleapis.com/css?family=Raleway:300,500);
@font-face {
  font-family: 'DDCHardware';
  src: url(/static/media/DDCHardware-Regular.e65630a3.otf);
  font-weight: bold;
}

@font-face {
  font-family: 'DDCHardware';
  src: url(/static/media/DDCHardware-Compressed.08caed58.otf);
  font-stretch: extra-condensed;
  font-weight: bold;
}

@font-face {
  font-family: 'DDCHardware';
  src: url(/static/media/DDCHardware-Condensed.363148e7.otf);
  font-stretch: condensed;
  font-weight: bold;
}



:root {
  --size: 16px;

  --color-bg: rgb(39, 39, 57);
  --color-fg: rgb(255, 252, 228);
  --color-accent-air: #49C1C1;
  --color-accent-metal: #E3B023;
  --color-accent-earth: #54AA3F;
  --color-accent-fire: #DE6735;
  --color-accent-water: #327CD1;
  --color-bg-active: #faee5b;
  --color-fg-active: #675F2A;

  --transition-duration: 300ms;
  --transition-timing-function: ease;
  --font-family-display: DDCHardware, sans-serif;
  /*--font-family-base: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;*/
  --font-family-base: Raleway, sans-serif;

  --font-weight-normal-base: 500;
  --font-weight-light-base: 300;

  --line-height-base: 2;
  --letter-spacing-base: 0;
}

*, *::before, *::after {
  box-sizing: border-box;
}

:root {
  background-color: var(--color-bg);
  color: var(--color-fg);
  font-size: var(--size);
  font-family: var(--font-family-base);
  line-height: var(--line-height-base);
  letter-spacing: var(--letter-spacing-base);
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-family-display);
  line-height: 1;
}

h2 {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

